import base from "./base"

export default {
  name: "apply",
  extendName: "single-shape",
  options: {
    ...base,
    shapeType: "path",
    getShapeStyle(cfg) {
      const size = this.getSize(cfg)
      const width = size[0]
      const height = size[1]
      const x = 0 - width
      const y = 0 - height
      const path = [
        // 
        ["M", -width / 2, 0],
        // 
        ["L", -width / 2, -height / 2],
        // 
        ["L", width / 2, -height / 2],
        // 
        ["L", width / 2, height / 2],
        // 
        ["L", -width / 2, height / 2],
        ["Z"],
      ]
      const color = cfg.color
      const style = Object.assign(
        {
          // 
          x,
          y,
          width,
          height,
          path,
          stroke: color,
        },
        cfg.style
      )
      return style
    },
  },
}
