import base from "./base"

export default {
  name: "task",
  extendName: "single-shape",
  options: {
    ...base,
    shapeType: "path",
    drawShape(cfg, group) {
      const shapeType = this.shapeType
      const style = this.getShapeStyle(cfg)
      const shape = group.addShape(shapeType, {
        attrs: style,
      })
      this.shape = shape
      return shape
    },
    getShapeStyle(cfg) {
      const size = this.getSize(cfg)
      const width = size[0]
      const height = size[1]
      const x = 0 - width / 2
      const y = 0 - height / 2
      const path = [
        // 
        ["M", -width / 2, 0],
        // 
        ["L", -width / 2, -height / 2],
        // 
        ["L", width / 2, -height / 2],
        // 
        ["L", width / 2, height / 2],
        // 
        ["L", -width / 2, height / 2],
        ["Z"],
      ]
      const color = cfg.color
      const style = Object.assign(
        {
          // 
          x,
          y,
          width,
          height,
          path,
          stroke: color,
        },
        cfg.style
      )
      return style
    },
  },
}
